import React, { Fragment } from 'react';
import './AppContainer.css';
import Header from '../common/Header/';
import SideBar from '../components/sidebar/';
import Data from '../components/data';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Dashboard from '../components/dashboard';
import ViewDataSet from '../components/data/datasets/view';
import BirdEye from "../components/birdsEyeView/"
import Connect from '../components/pipeline/connect';
import ConnectJobsOverview from '../components/pipeline/connect/jobs/jobs-overview';
import Process from '../components/pipeline/process';
import ProcessJobsOverview from '../components/pipeline/process/jobs/job-overview';
import Syndicate from '../components/pipeline/syndicate';
import SyndicateJobsOverview from '../components/pipeline/syndicate/jobs/job-overview';
import Login from '../components/login';
import Notifications from '../components/notification/'
import Customer360 from '../components/customer360/'
import Segments from '../components/segments/'
import CreateRule from '../components/notification/CreateRule';
import CreateSegment from '../components/segments/CreateSegment/'

const AppContainer = () => {

    return (
        <Fragment>
            <Router>
                <Header />
                <SideBar />
                <Switch>
                    <Route exact path="/dashboard">
                        <Dashboard />
                    </Route>
                    <Route path="/dataset" component={Data} />
                    <Route exact path="/datasetInfo" component={ViewDataSet} />
                    <Route exact path="/birdsView">
                        <BirdEye />
                    </Route>
                    <Route exact path="/notification" component={Notifications}/>
                    <Route exact path="/customer360" component={Customer360}/>
                    <Route exact path="/segmentView" component={Segments}/>
                    <Route exact path="/createRule" component={CreateRule}/>
                    <Route exact path="/createSegment" component={CreateSegment}/> 
                    <Route exact path="/connect">
                        <Connect />
                    </Route>
                    <Route exact path="/">
                        <Dashboard />
                    </Route>
                    <Route exact path="/job-detail-overview">
                        <ConnectJobsOverview />
                    </Route>
                    <Route exact path="/process">
                        <Process />
                    </Route>
                    <Route exact path="/process-detail-overview">
                        <ProcessJobsOverview />
                    </Route>
                    <Route exact path="/syndicate">
                        <Syndicate />
                    </Route>
                    <Route exact path="/syndicate-detail-overview">
                        <SyndicateJobsOverview />
                    </Route>
                    {/* <Route exact path="/">
                        <Login />
                    </Route> */}
                </Switch>

            </Router>
        </Fragment>
    );
};

export default AppContainer;