import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function MemoryUsage() {

    const options = {
        chart: {
            type: 'areaspline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"',

            },
            className: 'quality-trend'
        },
        title: {
            text: 'Memory Usage',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['g', 'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false
        },
        yAxis: {
            title: {
                text: 'Memory'
            },
            labels: {
                formatter: function () {
                    return this.value + 'GB';
                }
            }
        },
        tooltip: {
            pointFormat: '{series.name} processes consumed <b>{point.y}GB</b><br/>memory'
        },
        plotOptions: {
            areaspline: {
                stacking: 'normal',
                fillOpacity: 0.1,
                pointStart: 1,
                fill: false,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [{
            name: 'Marketing',
            data: [7, 7.8, 6.5, 6.8, 7.2, 7, 7.7, 7, 6.6, 7.4, 6.7, 7]
        }, {
            name: 'Transaction',
            data: [7, 7.8, 6.5, 6.8, 7.2, 7.7, 6.5, 7, 6.6, 7.4, 6.7, 7]
        }, {
            name: 'Customer',
            data: [5, 5.2, 4.8, 5, 4.7, 5.5, 4.8, 5.1, 4.8, 4.7, 5, 5.2]
        },
        {
            name: 'Unknown',
            data: [
                2.5, 2.3, 2.4, 5, 2.5, 2.2, 10, 2.3, 2.6, 2.5,
                2.5, 2.4
            ]
        }
        ]
    }

    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}
