import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function RecordProcess() {

    const options = {
        chart: {
            zoomType: 'xy',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"'
            },
            className: 'quality-trend'
        },
        title: {
            text: 'Records Processed',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },
        subtitle: {
            text: ''
        },
        xAxis: [{
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
                'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            crosshair: true
        }],
        yAxis: [{ // Primary yAxis
            labels: {
                format: '{value} mins',
                style: {
                    color: Highcharts.getOptions().colors[1]
                },
                marker:{
                enabled: false
                }
            },
            title: {
                text: 'Duration',
                style: {
                    color: Highcharts.getOptions().colors[1]
                }
            },
            opposite:true
        }, { // Secondary yAxis
            title: {
                text: '# of Records',
                style: {
                    color: Highcharts.getOptions().colors[1]
                }
            },
            labels: {
                format: '{value} M',
                style: {
                    color: Highcharts.getOptions().colors[1]
                }
            },
            opposite: false
        }],
        tooltip: {
            shared: true
        },
        series: [{
            name: '# of records',
            type: 'column',
            opacity: 0.5,
            yAxis: 1,
            data: [50, 60, 75, 80, 120, 125, 135, 120, 75, 70, 50, 40],
            tooltip: {
                valueSuffix: ' M'
            }
    
        }, {
            name: 'Duration',
            type: 'spline',
            data: [25, 30, 37, 40, 60, 62, 70, 60, 37, 35, 25, 20],
            tooltip: {
                valueSuffix: 'mins'
            }
        }]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}