/* import styled from "styled-components"; */
import { useTable, useFilters, useGlobalFilter } from "react-table";
import { Table } from 'reactstrap';
import SearchIcon from '@material-ui/icons/Search';

/* const Styles = styled.div`

  table {
      className: test
  }
   
`; */

// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter
}) {
    return (
        <Fragment>
            <div className="search-bar">
                <SearchIcon fontSize="inherit" className="search-icon" />
                <input
                className="search"
                    value={globalFilter || ""}
                    onChange={e => {
                        setGlobalFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                    }}
                    placeholder={`Search`}
                />
            </div>
        </Fragment>
    );
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, setFilter }
}) {
    return (
        <div className="search-bar">
            <input
                className="search"
                value={filterValue || ""}
                onChange={e => {
                    setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                }}
                placeholder="Search"
            />
            <SearchIcon fontSize="inherit" className="search-icon" />
        </div>

    );
}


// Our table component
function CustomTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            text: (rows, id, filterValue) => {
                return rows.filter(row => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                            .toLowerCase()
                            .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        state,
        preGlobalFilteredRows,
        setGlobalFilter
    } = useTable(
        {
            columns,
            data,
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes
        },
        useFilters,
        useGlobalFilter
    );

    // We don't want to render all of the rows for this example, so cap
    // it for this use case
    const firstPageRows = rows.slice(0, 10);
    return (
        <Fragment>
            <GlobalFilter
                preGlobalFilteredRows={preGlobalFilteredRows}
                globalFilter={state.globalFilter}
                setGlobalFilter={setGlobalFilter}
            />
            <Table {...getTableProps()}>
                <thead>
                    {headerGroups.map((headerGroup, index) => (
                        <tr {...headerGroup.getHeaderGroupProps()} key={index}>
                            {headerGroup.headers.map((column, index) => (
                                <th {...column.getHeaderProps()} key={index}>{column.render('Header')}</th>
                            ))}
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {firstPageRows.map((row, i) => {
                        prepareRow(row);
                        return (
                            <tr {...row.getRowProps()} key={i}>
                                {row.cells.map((cell,index) => {
                                    if (cell.column.Header === "State") {
                                        return <td className="danger" {...cell.getCellProps()}>{cell.render('Cell')}</td>
                                    } else {
                                        return (
                                            <td {...cell.getCellProps()} key={index}>{cell.render("Cell")}</td>
                                        );
                                    }                                    
                                })}
                            </tr>
                        );
                    })}
                </tbody>
            </Table>
            {/* {rows.length >= 10 ? <div>Showing the first 10 results of {rows.length} rows</div>:
                <div>Showing {rows.length} results.</div>            } */}
        </Fragment>
    );
}



