import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function QualityTrend() {
    const options = {
        chart: {
            type: 'areaspline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"',
            },
            className: 'quality-trend'
        },

        title: {
            text: 'Quality Trend',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            }
        },
        yAxis: {
            title: {
                text: '% Quality Test Passed'
            },
            labels: {
                formatter: function () {
                    return this.value + '%';
                }
            }
        },
        tooltip: {
            pointFormat: 'Quality test passed - <b>{point.y:,.0f}%</b>'
        },
        plotOptions: {
            areaspline: {
                fillOpacity: 0.1,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [
            {
                name: 'Quality Index',
                data: [70, 60, 70, 80, 70, 30, 70, 60, 70, 80, 60, 70
                ],
                zones: [
                    {
                        value: 40,
                        color: '#e53935'
                    },
                    {
                        value: 70,
                        color: '#ffb300'
                    },
                    { color: '#4caf50' }
                ]

            }
        ]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}
