import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function MemoryUsage() {

    const options = {
        chart: {
            type: 'areaspline'
          },
          title: {
            text: 'Memory Usage',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
          },
        
          xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
              formatter: function() {
                return this.value; // clean, unformatted number for year
              }
            },
            accessibility: {
              rangeDescription: 'Range: 1940 to 2017.'
            }
          },
          yAxis: {
            allowDecimals: false,
            title: {
              text: 'Memory'
            },
            labels: {
              formatter: function() {
                return this.value + ' MB';
              }
            },
            min: 0
          },
          tooltip: {
            pointFormat: '<b>{point.y}MB of memory consumed</b>'
          },
          plotOptions: {
            areaspline: {
            fillOpacity: 0.1,
        
              marker: {
                enabled: false,
                symbol: 'circle',
                radius: 0,
                states: {
                  hover: {
                    enabled: false
                  }
                }
              }
            }
          },
          series: [{
            name: 'Memory',
            data: [400, 350, 400, 450, 380, 420, 1200, 380, 400, 420, 380, 400]
          }]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}