import React, { Fragment, useState } from 'react';
import { Nav, NavItem, Row, Col, TabContent, TabPane } from 'reactstrap';
import { BrowserRouter as Router, Link } from "react-router-dom";
import './Process.css';
import Overview from './overview';
 import Jobs from './jobs';
import Task from '../../data/datasets/view/tasks';

export default function Process() {

    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>
            <div className="process-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="process-heading">Process</h2>
                    </Col>
                </Row>
                <Row className="navTabRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('1'); }}>
                                        Overview
                                    </Link>
                                </Router>
                            </NavItem>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '2' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('2'); }}>
                                        Jobs
                                    </Link>
                                </Router>
                            </NavItem>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '3' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('3'); }}>
                                        Tasks
                                    </Link>
                                </Router>
                            </NavItem>
                        </Nav>
                    </Col>
                </Row>
            </div>
            <div className="process-content">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Overview />
                    </TabPane>
                    <TabPane tabId="2">
                        <Jobs />
                    </TabPane>
                    <TabPane tabId="3">
                        <Task />
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}