import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function VolumeProcess() {

    const options = {
        chart: {
            type: 'spline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"'
            },
            className: 'quality-trend'
        },
        title: {
            text: 'Volume of Data Processed',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            },
            accessibility: {
                rangeDescription: 'Range: 1940 to 2017.'
            }
        },
        yAxis: {
            allowDecimals: false,
            title: {
                text: 'Volume'
            },
            labels: {
                formatter: function () {
                    return this.value + 'TB';
                }
            }
        },
        tooltip: {
            pointFormat: '<b>{point.y}TB of data processed</b>'
        },
        plotOptions: {
            spline: {

                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [{
            name: 'Marketing',
            data: [1, 0.5, 0.4, 0.8, 0.6, 0.9, 0.7, 1, 0.8, 1.4, 1.2, 1.8
            ]
        }, {
            name: 'Transaction',
            data: [null, null, null, 2, 5, 2.5, 6, 5, 7, 6, 6, 7
            ]
        }, {
            name: 'Customer',
            data: [1, 2, 1, 2, 1, 3, 1.5, 3, 1.5, 3.5,
                1.5, 2
            ]
        },
        {
            name: 'Unknown',
            data: [
                null, null, null, null, 3, 2, 5, 2.5, 6, 3,
                7, 3.5
            ]
        }]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )

}
