import React,{useState}  from 'react';
import '../Header/Header.css';
import { Row,Navbar,NavbarToggler,Input} from 'reactstrap';



function Header() {
    const [isOpen, setIsOpen] = useState(false);
    const toggle = () => setIsOpen(!isOpen);

    return (
        <Navbar color="light" light expand="md" id="header" className="fixed-top">
            <NavbarToggler onClick={toggle} />
            <Row>
                <Input type="text" name="search" id="exampleSearch" className="searchBar" placeholder="Type to search…" />
            </Row>
        </Navbar>
    );
};
export default Header
