import React, { Fragment, useState } from 'react';
import { Nav, NavItem, NavLink, Row, Col, TabContent, TabPane } from 'reactstrap';
import StarBorderIcon from '@material-ui/icons/StarBorder';
/* import {  Link} from "react-router-dom"; */
import './ViewDataset.css';
import Overview from './overview';
import DataDictionary from './data-dictionary'
import Lineage from './lineage/';
import Impact from './impact';
import SampleData from './sample-data/';
import DataQuality from './data-quality/';
import Discussions from './discussions/';
import Tasks from './tasks/';
import CustomerQuality from '../../../customer360/customerQuality';

export default function ViewDataSet(props) {
    const [activeTab, setActiveTab] = useState('1');
    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>
            <div className="dataSourceHeader fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="dataset-header">{props.location.state.datasetName}</h2>
                        <StarBorderIcon fontSize="small" className="starIcon" />
                    </Col>
                </Row>
                <Row className="navBarRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <NavLink className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('1'); }}>
                                    Overview
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '2' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('2'); }}>
                                    Data Dictionary
                            </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '3' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => {
                                        toggle('3');
                                    }}>
                                    Lineage
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '4' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => {
                                        toggle('4');
                                    }}>
                                    Impact
                            </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '5' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('5'); }}>
                                    Sample Data
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '6' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('6'); }}>
                                    Data Quality
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '7' ? 'nav-link' : 'disable nav-link'}
                                    onClick={() => { toggle('7'); }}>
                                    Discussions
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '8' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('8'); }}>
                                    Tasks
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </Col>
                </Row>
            </div>

            <div className="tabContent dataset-view" id="tab-content">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Overview />
                    </TabPane>
                    <TabPane tabId="2">
                        <DataDictionary />
                    </TabPane>
                    <TabPane tabId="3">
                        {activeTab === "3" && <Lineage currentDatasetId={props.location.state.dataSetId} />}
                    </TabPane>
                    <TabPane tabId="4">
                        {activeTab === "4" && <Impact />}
                    </TabPane>
                    <TabPane tabId="5" className="sampleData">
                        <SampleData />
                    </TabPane>
                    <TabPane tabId="6">
                        {/* <DataQuality /> */}
                        <CustomerQuality/>
                    </TabPane>
                    <TabPane tabId="7">
                        <Discussions />
                    </TabPane>
                    <TabPane tabId="8">
                        <Tasks />
                    </TabPane>
                </TabContent>
            </div>
        </Fragment>

    );

}