import React, { Fragment, useState, useEffect } from 'react';
import { Nav, NavItem, Row, Col, TabContent, TabPane } from 'reactstrap';
import { BrowserRouter as Router, Link } from "react-router-dom";
import './DatasetNavbar.css';
import ListDataset from '../list';
import Domains from '../../domains/';

export default function DatasetNavbar(props) {

    const [activeTab, setActiveTab] = useState(1);
    const [setId, setTabId] = useState(true);

    useEffect(() => {
        if (props.tabId && setId) {
            setActiveTab(props.tabId.tabId)
        }
    })

    const toggle = tab => {
        setTabId(false)
        if (activeTab !== tab) setActiveTab(tab);
    }

    return (
        <Fragment>
            <div className="dataset-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="datset-heading">Datasets</h2>
                    </Col>
                </Row>
                <Row className="navBarRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === 1 ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle(1); }}>
                                        Domains
                                </Link>
                                </Router>
                            </NavItem>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === 2 ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle(2); }}>
                                        Datasets
                                </Link>
                                </Router>
                            </NavItem>

                        </Nav>
                    </Col>
                </Row>
            </div>
            <div className="tabContent" style={{marginTop:"120px"}}>
                <TabContent activeTab={activeTab}>
                    <TabPane tabId={1}>
                        <Domains />
                    </TabPane>
                    <TabPane tabId={2}>
                        <ListDataset />
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}