import React, { Fragment, useState } from 'react';
import { Nav, NavItem, NavLink, Row, Col, TabContent, TabPane, Button } from 'reactstrap';
import './switchTab.css';
import CurrentQuality from './currentQuality/'
import QualityOverTime from './qualityOvertime/index'

export default function SwitchTab() {

    const [activeTab, setActiveTab] = useState('1');
    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>
            <div className="customer-quality">
                <Nav className="mx-5 my-4">
                            <NavItem>
                                <NavLink className={activeTab === '1' ? 'active nav-link btn btn-primary rounded-0' : 'nav-link btn btn-default rounded-0 text-primary'}
                                    onClick={() => { toggle('1'); }}>
                                    Current Quality
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '2' ? 'active nav-link btn btn-primary rounded-0' : 'nav-link btn btn-default rounded-0 text-primary'}
                                    onClick={() => { toggle('2'); }}>
                                    Quality Overtime
                                </NavLink>
                            </NavItem>
                        </Nav>
            </div>
            <div className="tabContent">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <CurrentQuality/>
                    </TabPane>
                    <TabPane tabId="2">
                        <QualityOverTime/>
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}