import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function DashboardShare() {

    const options = {
        chart: {
            type: 'areaspline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"',

            },
            // className: 'share-chart'
            className: 'quality-trend'
        },
        title: {
            text: '% Share'
        },

        xAxis: {
            categories: ['g', 'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false
        },
        yAxis: {
            title: {
                text: '% Volume'
            },
            labels: {
                formatter: function () {
                    return this.value + '%';
                }
            }
        },
        tooltip: {
            pointFormat: '{series.name} has <b>{point.y:,.0f}%</b><br/>volume share'
        },
        plotOptions: {
            areaspline: {
                stacking: 'percent',
                fillOpacity: 0.1,
                pointStart: 1,
                fill: false,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [{
            name: 'Marketing',
            data: [null, null, null, null, 20, 30, 20, 25, 20, 15, 12, 8]
        }, {
            name: 'Transaction',
            data: [null, null, 5, 20, 11, 15, 20, 18, 25, 20, 18, 20]
        }, {
            name: 'Customer',
            data: [20, 25, 25, 18, 10, 8, 4, 6, 9, 6, 5, 30]
        },
        {
            name: 'Unknown',
            data: [
                null, null, null, null, null, 6, 11, 12, 10, 9,
                5, 15
            ]
        }
        ]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}