// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter
}) {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen(prevState => !prevState);
    return (
        <Fragment>
            <div className="search-bar">
                <input
                    className="search"
                    value={globalFilter || ""}
                    onChange={e => {
                        setGlobalFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                    }}
                    placeholder="Search"
                />
                <SearchIcon fontSize="inherit" className="search-icon" />
            </div>

            <Dropdown isOpen={dropdownOpen} toggle={toggle} className="version-dropdown">
                <DropdownToggle caret>
                    Latest
                        </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem>Version 1</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem>Version 2</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem>Version 3</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem>Version 4</DropdownItem>
                </DropdownMenu>
            </Dropdown>
            <strong className="version-info">
                Version:
            </strong>

        </Fragment>
    );
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, preFilteredRows, setFilter }
}) {
    const count = preFilteredRows.length;

    return (
        <input
            value={filterValue || ""}
            onChange={e => {
                setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
            }}
            placeholder={`Search ${count} records...`}
        />
    );
}


