import React, { Fragment, useEffect } from "react";
import '../../../../../data/domains/Domains.css'
import { useTable, useFilters, useGlobalFilter, useExpanded, useSortBy } from "react-table";
import { Table, Progress } from 'reactstrap';
import SearchIcon from '@material-ui/icons/Search';
import volumeGrowth from '../../../../../../assets/icons/volumeGrowth.png'
import alertIcon from '../../../../../../assets/icons/alert.png'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import data from "../../../../../../common/qualityTable.json"

// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter
}) {
    return (
        <Fragment>
        </Fragment>
    );
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, setFilter }
}) {
    return (
        <div className="search-bar">
            <input
                className="search"
                value={filterValue || ""}
                onChange={e => {
                    setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                }}
                placeholder="Search"
            />
            <SearchIcon fontSize="inherit" className="search-icon" />
        </div>

    );
}


// Our table component
function CustomTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            text: (rows, id, filterValue) => {
                return rows.filter(row => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                            .toLowerCase()
                            .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    function renderProgressBar(cell) {
        if (cell.value <= 50) {
            return <Progress color="danger" value={cell.value} />
        } else if (cell.value > 50 && cell.value < 100) {
            return <Progress color="warning" value={cell.value} />
        } else if (cell.value >= 100) {
            return <Progress color="success" value={cell.value} />
        }
    }

    function renderAlertIcon(cell) {
        if (cell.row.id == 0) {
            return <img src={alertIcon} className="alertIcon" />
        }
    }

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        state,
        preGlobalFilteredRows,
        setGlobalFilter
    } = useTable(
        {
            columns,
            data,
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes
        },
        useFilters,
        useGlobalFilter,
        useExpanded,
        useSortBy
    );

    // We don't want to render all of the rows for this example, so cap
    // it for this use case
    const firstPageRows = rows.slice(0, 10);
    return (
        <Fragment>
            <GlobalFilter
                preGlobalFilteredRows={preGlobalFilteredRows}
                globalFilter={state.globalFilter}
                setGlobalFilter={setGlobalFilter}
            />
            <Table {...getTableProps()}>
                <thead>
                    {headerGroups.map((headerGroup, index) => (
                        <tr key={index}{...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map((column, i) => (
                                <th key={i}  {...column.getHeaderProps(column.getSortByToggleProps())}>
                                    {column.render('Header')}
                                    <span className="sorting-arrow">
                                        {column.isSorted
                                            ? column.isSortedDesc
                                                ? <ArrowDropDownIcon className="down-arrow" />
                                                : <ArrowDropUpIcon className="up-arrow" />
                                            : ''}
                                    </span>
                                </th>
                            ))}
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {firstPageRows.map((row, i) => {
                        prepareRow(row);
                        return (
                            <>
                            <tr {...row.getRowProps()} key={i}>
                                {row.cells.map((cell, index) => {
                                    if (cell.column.Header === "Result") {
                                        return (
                                            <td {...cell.getCellProps()} key={index}>{cell.render("Cell")}
                                                {renderProgressBar(cell)}
                                            </td>
                                        );
                                    } else if (cell.column.Header === "Trend") {
                                        return (
                                            <td {...cell.getCellProps()} key={index}>
                                                <img src={volumeGrowth} />
                                            </td>
                                        );
                                    } else {
                                        return (
                                            <td {...cell.getCellProps()} key={index}>  {cell.render("Cell")}
                                            </td>
                                        );
                                    }
                                })}
                            </tr>
                        </>
                        );
                    })}
                </tbody>
            </Table>
        </Fragment>
    );
}

export default function QualityTable() {

    const columns = React.useMemo(
        () => [

            {
                Header: "Rule",
                accessor: "rule"
            },
            {
                Header: "Result",
                accessor: "result"
            },
            {
                Header: "Comments",
                accessor: "comments"
            },
            {
                Header: "Trend",
                accessor: "trend"
            }
        ],
        []
    );

    /* const data = React.useMemo(() => makeData(100000), []); */

    return (
        <div className="w-100 m-0 p-0">
            <CustomTable columns={columns} data={data} />
        </div>
    );
}
