import React, { Fragment, useState } from 'react';
import { Nav, NavItem, Row, Col, TabContent, TabPane } from 'reactstrap';
import { BrowserRouter as Router, Link } from "react-router-dom";
import './Connect.css';
import VolumeChart from './volume-chart';
import RecordImported from './record-imported';
import MemoryUsage from './memory-usage';
import CpuUsage from './cpu-usage';
import UpdatedDate from './updated-date';
import ImportedData from './imported-data';
import ImportedRecord from './imported-record';
import ConnectSummary from './connect-summary';
import Task from '../../data/datasets/view/tasks';
import Jobs from './jobs';

export default function Connect() {

    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return(
        <Fragment>
            <div className="connect-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="connect-heading">Connect</h2>
                    </Col>
                </Row>
                <Row className="navTabRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('1'); }}>
                                        Overview
                                    </Link>
                                </Router>
                            </NavItem>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '2' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('2'); }}>
                                        Jobs
                                    </Link>
                                </Router>
                            </NavItem>
                            <NavItem>
                                <Router>
                                    <Link to="" className={activeTab === '3' ? 'active nav-link' : 'nav-link'}
                                        onClick={() => { toggle('3'); }}>
                                        Tasks
                                    </Link>
                                </Router>
                            </NavItem>
                        </Nav>
                    </Col>
                </Row>
            </div>
            <div className="connect-content">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Row className="connect-container">
                            <Col xs="9">
                                <div className="connect-charts">
                                    <Row>
                                        <Col xs="6">
                                            <VolumeChart/>
                                        </Col>
                                        <Col xs="6">
                                            <RecordImported />
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col xs="6">
                                            <MemoryUsage/>
                                        </Col>
                                        <Col xs="6">
                                            <CpuUsage />
                                        </Col>
                                    </Row>
                                </div>
                            </Col>
                            <Col xs="3" className="d-flex justify-content-end">
                                <div className="right-section">
                                    <UpdatedDate/>
                                    <Row>
                                        <Col xs="6" className="d-flex justify-content-start">
                                            <ImportedData title="14TB" value="Imported" />
                                        </Col>
                                        <Col xs="6" className="d-flex justify-content-end">
                                            <ImportedRecord title="384M" value="Imported"/>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col xs="12">
                                            <ConnectSummary />                                            
                                        </Col>
                                    </Row>
                                </div>
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="2">
                        <Jobs />
                    </TabPane>
                    <TabPane tabId="3">
                        <Task />
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}