import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function ValidRecordTrend() {

    const options = {
        chart: {
            type: 'spline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"'
            },
            className: 'data-growth'
        },

        title: {
            text: 'Valid Record Trend',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            }
        },
        yAxis: {
            title: {
                text: '# of Valid Records'
            },
            labels: {
                formatter: function () {
                    return this.value + 'K';
                }
            }
        },
        tooltip: {
            pointFormat: 'Valid Records - <b>{point.y:,.0f}K</b>'
        },
        plotOptions: {
            spline: {
                fillOpacity: 0.1,
                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [
            {
                name: 'Valid Records',
                data: [34, 44, 34, 54, 24, 64, 54, 84, 64, 54, 64, 44
                ]
                //        color: '#e53935'

            }
        ]
    }

    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}