// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter
}) {
    return (
        <Fragment>
            <div className="search-bar">
                <input
                    className="search"
                    value={globalFilter || ""}
                    onChange={e => {
                        setGlobalFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                    }}
                    placeholder="Search"
                />
                <SearchIcon fontSize="inherit" className="search-icon" />
            </div>


        </Fragment>
    );
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, preFilteredRows, setFilter }
}) {
    const count = preFilteredRows.length;

    return (
        <input
            value={filterValue || ""}
            onChange={e => {
                setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
            }}
            placeholder={`Search ${count} records...`}
        />
    );
}

// Our table component
function CustomTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            text: (rows, id, filterValue) => {
                return rows.filter(row => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                            .toLowerCase()
                            .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    function renderLinkArr(cell) {
        let linkarr = []

        cell.value.map((v, i) => {
            if ((i + 1) === cell.value.length) {
                linkarr.push(<Link to="#" key={i}> {v} </Link>)
            } else {
                linkarr.push(<Link to="#" key={i}> {v}, </Link>)
            }
        })

        return linkarr
    }

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        state,
        preGlobalFilteredRows,
        setGlobalFilter,
        /* state: { expanded } */
    } = useTable(
        {
            columns,
            data,
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes
        },
        useFilters, // useFilters!
        useGlobalFilter, // useGlobalFilter!
        useSortBy
    );

    // We don't want to render all of the rows for this example, so cap
    // it for this use case
    /* const firstPageRows = rows.slice(0, 10); */

    return (
        <Fragment>
            <GlobalFilter
                preGlobalFilteredRows={preGlobalFilteredRows}
                globalFilter={state.globalFilter}
                setGlobalFilter={setGlobalFilter}
            />
            <Button color="primary">+ Request New</Button>
            <Table {...getTableProps()}>
                <thead>
                    {headerGroups.map((headerGroup, index) => (
                        <tr key={index}{...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map((column, index) => (
                                <th key={index}  {...column.getHeaderProps(column.getSortByToggleProps())}>
                                    {column.render('Header')}
                                    <span className="sorting-arrow">
                                        {column.isSorted
                                            ? column.isSortedDesc
                                                ? <ArrowDropDownIcon className="down-arrow" />
                                                : <ArrowDropUpIcon className="up-arrow" />
                                            : ''}
                                    </span>
                                </th>
                            ))}
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {rows.map((row, index) => {
                        prepareRow(row)
                        return (
                            <>
                                <tr key={index} {...row.getRowProps()}>
                                    {row.cells.map((cell, i) => {
                                        if (cell.column.Header === "Owner") {
                                            return <td key={i} className="clickableCell" {...cell.getCellProps()}>
                                                {renderLinkArr(cell)}</td>
                                        } else {
                                            return <td key={i} {...cell.getCellProps()}>
                                                {cell.render('Cell')}
                                            </td>
                                        }
                                    })}
                                </tr>
                            </>
                        )
                    })}
                </tbody>


            </Table>


            <div className="footer-buttons">
                <Button color="success">Has PII</Button>
                <Button color="info">Has PCI</Button>
                <Button color="warning">Masking</Button>
            </div>

            {/* <div>Showing the first 20 results of {rows.length} rows</div> */}
        </Fragment>
    );
}

