import React, { Fragment, useEffect, useState } from 'react';
import './SideBar.css'
import { Link, NavLink } from "react-router-dom";

function SideBar() {

    const [activeItem, setActiveItem] = useState('')

    function setActiveLink(e, activeItem) {
        setActiveItem(activeItem)
    }

    useEffect(() => {
        var iconElement;
        document.getElementById("sidebar").onmouseover = function () {
            iconElement = document.getElementsByClassName('rubik-icon-small');
            mouseOver(iconElement);
        }

        document.getElementById("sidebar").onmouseout = function () {
            iconElement = document.getElementsByClassName('rubik-icon-small');
            mouseOut(iconElement);
        }
        function mouseOver(iconElement) {
            for (var index = 0; index < iconElement.length; index++) {
                iconElement[index].style["display"] = 'none';
            }
        }
        function mouseOut(iconElement) {
            for (var index = 0; index < iconElement.length; index++) {
                iconElement[index].style["display"] = 'block';
            }
        }

    });
    return (
        <Fragment>
            <nav id="sidebar">
                <ul>
                    <li className="companyLogo">
                        <Link to="#" className="disabledLink" onClick={(event) => event.preventDefault()}>
                            <i className="rubik-icon-small"></i>
                            <i className="rubik-icon-large"></i>
                        </Link>
                    </li>
                    <li className="parent-list-2">
                        <Link to="/dashboard">
                            <span className="app-icon"></span>
                            <span className="nav-text">
                                APPS
                            </span>
                        </Link>
                        <ul className="child-list">
                            <li className={activeItem == "dashboard" ? "active" : ''} onClick={e => setActiveLink(e, "dashboard")}>
                                <Link to="dashboard" active className="active">
                                    <span className="dashboard-icon"></span>
                                    <span className="nav-text">
                                        Dashboard
                            </span>
                                </Link >

                            </li>
                            <li className={activeItem == "birdeye" ? "active" : ''} onClick={e => setActiveLink(e, "birdeye")}>
                                <Link to="birdsView">
                                    <span className="birdeye-icon"></span>
                                    <span className="nav-text">
                                        Bird's eye
                            </span>
                                </Link>

                            </li>
                            <li className={activeItem == "customer360" ? "active" : ''} onClick={e => setActiveLink(e, "customer360")}>
                                <Link to="customer360">
                                    <span className="customer-icon"></span>
                                    <span className="nav-text">
                                        Customer 360
                            </span>
                                </Link>
                            </li>
                            <li className={activeItem == "notification" ? "active" : ''} onClick={e => setActiveLink(e, "notification")}>
                                <Link to="notification">
                                    <span className="notification-icon"></span>
                                    <span className="nav-text">
                                        Notifications
                            </span>
                                </Link>
                            </li>
                            <li className={activeItem == "segments" ? "active" : ''} onClick={e => setActiveLink(e, "segments")}>
                                <Link to="segmentView">
                                    <span className="segment-icon"></span>
                                    <span className="nav-text">
                                        Segments
                            </span>
                                </Link>
                            </li>
                        </ul>
                    </li>




                    <li className={activeItem == "dataset" ? "active" : ''} onClick={e => setActiveLink(e, "dataset")}>
                        <Link to="dataset">
                            <span className="dataset-icon"></span>
                            <span className="nav-text">
                                datasets
                            </span>
                        </Link>
                    </li>
                    <li className="parent-list">
                        <Link to="/connect">
                            <span className="pipeline-icon"></span>
                            <span className="nav-text">
                                pipeline
                            </span>
                        </Link>
                        <ul className="child-list">
                            <li className={activeItem == "connect" ? "active" : ''} onClick={e => setActiveLink(e, "connect")}>
                                <Link to="connect">
                                    <span className="connect-icon"></span>
                                    <span className="nav-text">
                                        connect
                                    </span>
                                </Link>
                            </li>
                            <li className={activeItem == "process" ? "active" : ''} onClick={e => setActiveLink(e, "process")}>
                                <Link to="process">
                                    <span className="process-icon"></span>
                                    <span className="nav-text">
                                        process
                                    </span>
                                </Link>
                            </li>
                            <li className={activeItem == "syndicate" ? "active" : ''} onClick={e => setActiveLink(e, "syndicate")}>
                                <Link to="syndicate">
                                    <span className="syndicate-icon"></span>
                                    <span className="nav-text">
                                        syndicate
                                    </span>
                                </Link>
                            </li>
                        </ul>
                    </li>
                    <li>
                        <Link to="#" className="disabledLink" onClick={(event) => event.preventDefault()}>
                            <span className="admin-icon"></span>
                            <span className="nav-text">
                                admin
                            </span>
                        </Link>
                    </li>
                    <li>
                        <Link to="#" className="disabledLink" onClick={(event) => event.preventDefault()}>
                            <span className="sql-icon"></span>
                            <span className="nav-text">
                                sql explorer
                            </span>
                        </Link>
                    </li>

                    <li className="bottom-items">
                        <Link to="#" className="disabledLink" onClick={(event) => event.preventDefault()}>
                            <span className="logout-icon"></span>
                            <span className="nav-text">
                                logout
                            </span>
                        </Link>
                    </li>
                    <li className="bottom-items">
                        <Link to="#" className="disabledLink" onClick={(event) => event.preventDefault()}>
                            <span className="documentation-icon"></span>
                            <span className="nav-text">
                                documentation
                            </span>
                        </Link>
                    </li>



                </ul>

            </nav>

        </Fragment>
    )
}

export default SideBar;