import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function DashboardGrowth() {
    const options = {
        chart: {
            type: 'spline',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"'
            },
            className: 'data-growth'
        },
        title: {
            text: 'Growth',
            align: 'center',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'normal'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            },
            accessibility: {
                rangeDescription: 'Range: 1940 to 2017.'
            }
        },
        yAxis: {
            title: {
                text: 'Volume in TB'
            },
            labels: {
                formatter: function () {
                    return this.value + 'TB';
                }
            }
        },
        tooltip: {
            pointFormat: 'Total Volume of {series.name} is <b>{point.y}TB</b>'
        },
        plotOptions: {
            spline: {

                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [{
            name: 'Marketing',
            data: [0.3, 0.4, 0.5, 0.6, 0.8, 1, 1, 1.1, 1.3, 1.6, 2.0, 2.3
            ]
        }, {
            name: 'Transaction',
            data: [null, null, 0.5, 0.7, 1.1, 1.5, 2.0, 2.0, 2.5, 2.2, 2.8, 2.8
            ]
        }, {
            name: 'Customer',
            data: [0.2, 0.25, 0.25, 0.28, 0.4, 0.8, 0.8, 1.1, 1.2, 1.6, 1.8, 2.0
            ]
        },
        {
            name: 'Unknown',
            data: [
                null, null, null, null, null, 0.6, 1.1, 1.2, 1.4, 1.5,
                1.6, 1.7
            ]
        }]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}