import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';
import { Container, Row, Col } from 'reactstrap';
export default function AgeDistribution() {

    var pieColors = (function() {
  var colors = [],
    base = Highcharts.getOptions().colors[0],
    i;

  for (i = 0; i < 10; i += 1) {
    // Start out with a darkened base color (negative brighten), and end
    // up with a much brighter color
    colors.push(Highcharts.color(base).brighten((i - 3) / 7).get());
  }
  return colors;
}())

    const options = {
        chart: {
    plotBackgroundColor: null,
    plotBorderWidth: null,
    plotShadow: false,
    type: 'pie',
    className:'quality-trend' 
  },
  title: {
    text: '',
    align:'left'
  },
  tooltip: {
    pointFormat: '{series.name}: <b>{point.percentage:.1f}K</b>'
  },
  accessibility: {
    point: {
      valueSuffix: '%'
    }
  },
  plotOptions: {
    pie: {
      opacity: 0.7,
      allowPointSelect: true,
      cursor: 'pointer',
      colors: pieColors,
      dataLabels: {
        enabled: false,
        format: '<b>{point.name}</b><br>{point.percentage:.1f} K',
        distance: 0,
        filter: {
          property: 'percentage',
          operator: '>',
          value: 4
        }
      },
      showInLegend: true
    }
  },
  series: [{
    name: 'Population',
    data: [{
        name: '45-55 Years',
        y: 61.41
      },
      {
        name: '35-45 Years',
        y: 21.84
      },
      {
        name: '25-35 Years',
        y: 10.85
      },
      {
        name: '15-25 Years',
        y: 7.67
      },
      {
        name: '55+ Years',
        y: 4.18
      }
    ]
  }]
    }
    return (
      <>
      <Row className="mt-4">
<h4 className="subHead m-0 ml-4 mb-3">Age Distribution &nbsp;</h4>
      </Row>
      <HighchartsReact
          highcharts={Highcharts}
          options={options}
      />
  </>
)
}
