import React, { Fragment, useState, useEffect } from 'react';
import './ListDataset.css'
import { useTable, useFilters, useGlobalFilter, useExpanded, useSortBy, usePagination } from "react-table";
import { Table, ButtonDropdown, Dropdown, DropdownToggle, DropdownMenu, DropdownItem, Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import SearchIcon from '@material-ui/icons/Search';
/* import ExpandLessIcon from '@material-ui/icons/ExpandLess'; */
/* import ExpandMoreIcon from '@material-ui/icons/ExpandMore'; */
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import { Link } from "react-router-dom";
import data from '../../../../common/dataSets.json'

// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter
}) {
    /*  const [dropdownOpen, setDropdownOpen] = useState(false);
     const toggle = () => setDropdownOpen(prevState => !prevState); */
    return (
        <Fragment>
            <div className="search-bar">
                <input
                    className="search"
                    value={globalFilter || ""}
                    onChange={e => {
                        setGlobalFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
                    }}
                    placeholder="Search"
                />
                <SearchIcon fontSize="inherit" className="search-icon" />
            </div>

        </Fragment>
    );
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, preFilteredRows, setFilter }
}) {
    const count = preFilteredRows.length;

    return (
        <input
            value={filterValue || ""}
            onChange={e => {
                setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
            }}
            placeholder={`Search ${count} records...`}
        />
    );
}

// Our table component
function CustomTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            text: (rows, id, filterValue) => {
                return rows.filter(row => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                            .toLowerCase()
                            .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    function renderLinkArr(cell) {
        let linkarr = []        
        cell.value.map((v, i) => {
            if ((i + 1) === cell.value.length) {
                linkarr.push(<Link to="#"> {v} </Link>)
            } else {
                linkarr.push(<Link to="#"> {v}, </Link>)
            }
        })

        return linkarr
    }

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        page,
        prepareRow,
        state,
        preGlobalFilteredRows,
        setGlobalFilter,
        canPreviousPage,
        canNextPage,
        pageOptions,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize },
    } = useTable(
        {
            columns,
            data,
            initialState: { pageIndex: 0 },
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes
        },
        useFilters, // useFilters!
        useGlobalFilter, // useGlobalFilter!
        useExpanded,
        useSortBy,
        usePagination
    );

    // We don't want to render all of the rows for this example, so cap
    // it for this use case
    /* const firstPageRows = rows.slice(0, 10); */

    

    function getPaginationIndex() {

        if (pageOptions.length > 0) {
            var arr = [];
            for (let index = 1; index <= pageOptions.length; index++) {
                arr.push(
                    <PaginationItem onClick={e => setActiveIndex(index)} className={index === pageIndex+1?'active':""}>
                        <PaginationLink >
                            {index}
                        </PaginationLink>
                    </PaginationItem>
                )
            }
            return arr;

        }
    }
    function setActiveIndex(index) {
        gotoPage(index-1)
    }

    return (
        <Fragment>
            <GlobalFilter
                preGlobalFilteredRows={preGlobalFilteredRows}
                globalFilter={state.globalFilter}
                setGlobalFilter={setGlobalFilter}
            />
            <Table {...getTableProps()}>
                <thead>
                    {headerGroups.map((headerGroup, index) => (
                        <tr key={index}{...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map((column, index) => (
                                <th key={index}  {...column.getHeaderProps(column.getSortByToggleProps())}>
                                    {column.render('Header')}
                                    <span className="sorting-arrow">
                                        {column.isSorted
                                            ? column.isSortedDesc
                                                ? <ArrowDropDownIcon className="down-arrow" />
                                                : <ArrowDropUpIcon className="up-arrow" />
                                            : ''}
                                    </span>
                                </th>
                            ))}
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {page.map((row, i) => {
                        prepareRow(row)
                        return (
                            <>
                                <tr key={i} {...row.getRowProps()}>
                                    {row.cells.map((cell, d) => {
                                        if (cell.column.Header === "Entities" || cell.column.Header === "Owner" || cell.column.Header === "Domains") {
                                            return <td key={d} className="clickableCell" {...cell.getCellProps()}>
                                                {renderLinkArr(cell)}</td>
                                        } else if (cell.column.Header === "Description") {
                                            return <td key={d} className="desCell" {...cell.getCellProps()}>{cell.render('Cell')}</td>
                                        } else if (cell.column.Header === "Name") {
                                            return <td key={d} className="cellContent" {...cell.getCellProps()}><Link to={{pathname:"datasetInfo", state:{datasetName: cell.value, dataSetId: cell.row.original.id}}}>{cell.render('Cell')}</Link></td>
                                        } else {
                                            return <td key={d} className="cellContent" {...cell.getCellProps()}>{cell.render('Cell')}</td>
                                        }
                                    })}
                                </tr>
                            </>
                        )
                    })}
                </tbody>

            </Table>


            <Pagination>
                <PaginationItem>
                    <PaginationLink first onClick={() => gotoPage(0)} disabled={!canPreviousPage} />
                </PaginationItem>
                    {getPaginationIndex()}
                <PaginationItem>
                    <PaginationLink last onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage} />
                </PaginationItem>


            </Pagination>


        </Fragment>
    );
}

export default function ListDataset() {
    const [dropdownOpen, setOpen] = useState(false);
    const toggle = () => setOpen(!dropdownOpen);

    console.log('clientWidth',document.getElementById('root').clientWidth);
    const columns = React.useMemo(
        () => [

            {
                Header: "Name",
                accessor: "name"
            },
            {
                Header: "Description",
                accessor: "description"
            },
            {
                Header: "Labels",
                accessor: "label"
            },
            {
                Header: "Updated",
                accessor: "updated"
            },
            {
                Header: "Owner",
                accessor: "owner"
            },
            {
                Header: "Domains",
                accessor: "domain"
            },
            {
                Header: "Entities",
                accessor: "entities"
            },
        ],
        []
    );

    return (
        <div className="list-dataset">
            <ButtonDropdown isOpen={dropdownOpen} toggle={toggle}>
                <DropdownToggle caret>
                    Filter
                </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem>Item 1</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem>Item 2</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem>Item 3</DropdownItem>
                </DropdownMenu>
            </ButtonDropdown>
            <CustomTable columns={columns} data={data} />
        </div>
    );
}
