import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function CpuUsage() {

    const options = {
        chart: {
            type: 'column',
            style: {
                fontFamily: '"Helvetica","Roboto", "Arial"'
            },
            className: 'data-growth'
        },

        title: {
            text: 'CPU Usage',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            }
        },
        yAxis: {
            title: {
                text: '# of Cores'
            },
            labels: {
                formatter: function () {
                    return this.value;
                }
            }
        },
        tooltip: {
            pointFormat: '{series.name} processes consume <b>{point.y} cores</b><br/>'
        },
        plotOptions: {
            column: {
                opacity: 0.5,
                stacking: 'normal',

                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [
            {
                name: 'Marketing',
                data: [4, 4, 3, 4, 4, 4, 4, 12, 4, 3, 4, 4
                ]

            },
            {
                name: 'Transaction',
                data: [8, 8, 8, 6, 8, 8, 8, 8, 8, 6, 8, 8
                ]

            },
            {
                name: 'Customer',
                data: [2, 2, 2, 2, 2, 6, 2, 2, 2, 2, 2, 2
                ]

            },
            {
                name: 'Unknown',
                data: [1, 1, 1, 1, 1, 1, 8, 1, 1, 1, 1, 1
                ]

            }
        ]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}