import React, { useEffect, useState } from 'react';
import LineageChart from './chart';

function Lineage(props) {
    const [nodes, setNode] = useState([]);
    const [links, setLinks] = useState([]);

    useEffect(() => {
        let currentDataSetId = props.currentDatasetId

        let currentDatasetId = "550301e3-52a9-439b-a1a1-ac3c6ce534dd";
        let rawData = [{ "uuid": "fe916304-c510-4c82-adbb-d84dfe505bae", "snapshotId": 1580743373828, "datasetUuid": "550301e3-52a9-439b-a1a1-ac3c6ce534dd", "jobUuid": "9c0ff722-f6aa-4f9a-8df2-40503d67325c", "jobVersionUuid": "9d11d03f-d1a0-4ad2-8501-fad52380bd07", "jobInputUuids": ["6e8944d3-a0fa-4d2a-813a-201deaa23a6d", "0e7d95de-9897-4964-b71c-1531a9ec2f5d"], "jobOutputUuids": ["550301e3-52a9-439b-a1a1-ac3c6ce534dd"], "createdAt": 1580743373.851000000 }, { "uuid": "49df3856-50c2-4199-9670-471e1f8069b7", "snapshotId": 1580743373828, "datasetUuid": "550301e3-52a9-439b-a1a1-ac3c6ce534dd", "jobUuid": "f4c5a014-a4d1-44cc-904a-cac01971b92a", "jobVersionUuid": "d66a5a52-7f9d-4cd9-84ac-ec08c2729705", "jobInputUuids": ["ce581367-7175-42f6-9063-21268d1cdbd7", "776540f0-24ad-45b4-9f96-bc4a45fb3bcb"], "jobOutputUuids": ["0e7d95de-9897-4964-b71c-1531a9ec2f5d"], "createdAt": 1580743373.883000000 }, { "uuid": "b3621e49-554d-4bd3-90e2-9ea9379815c9", "snapshotId": 1580743373828, "datasetUuid": "550301e3-52a9-439b-a1a1-ac3c6ce534dd", "jobUuid": "e790169b-e46b-4f8a-a697-df803786b388", "jobVersionUuid": "7a166b43-b775-4ea6-92c4-2f3c1d7d2dee", "jobInputUuids": ["8017db99-2134-4626-aef3-27ecc6c4f594", "0450cb98-534b-455d-b4f4-332092e4b0b6"], "jobOutputUuids": ["0e7d95de-9897-4964-b71c-1531a9ec2f5d"], "createdAt": 1580743373.893000000 }]

        let nodesD = {},
            jobs = {},
            linksD = {},
            datasets = {};
        let jobList = [];

        rawData.map(data => {

            nodesD[data.jobUuid] = {
                "id": data['jobUuid'],
                "type": "job",
                "level": 0
            }

            jobs[data.jobUuid] = {
                "inputs": data.jobInputUuids,
                "outputs": data.jobOutputUuids
            }

            data.jobInputUuids.map(input => {
                nodesD[input] = {
                    "id": input,
                    "type": "dataset",
                    "level": 0
                }
                let tempKey = input + data.jobUuid
                let link = {
                    "source": input,
                    "target": data.jobUuid
                }

                linksD[tempKey] = link

                let inputArr = []

                if (Object.keys(datasets).indexOf(input) !== -1) {

                    if (!datasets[input]['outputJobs']) {
                        datasets[input] = {}
                        inputArr.push(data.jobUuid)
                        datasets[input]['outputJobs'] = inputArr
                    } else {
                        datasets[input]['outputJobs'].push(data.jobUuid);
                    }

                } else {
                    datasets[input] = {}
                    inputArr.push(data.jobUuid)
                    datasets[input]['outputJobs'] = inputArr
                }

            });

            // for output links 

            data.jobOutputUuids.map(output => {
                nodesD[output] = {
                    "id": output,
                    "type": "dataset",
                    "level": 0
                }
                let tempKey = output + data.jobUuid
                let link = {
                    "source": data.jobUuid,
                    "target": output
                }

                linksD[tempKey] = link

                let outputArr = []

                if (Object.keys(datasets).indexOf(output) !== -1) {

                    if (!datasets[output]['inputJobs']) {
                        outputArr.push(data.jobUuid)
                        datasets[output]['inputJobs'] = outputArr
                    } else {
                        datasets[output]['inputJobs'].push(data.jobUuid);
                    }

                } else {
                    datasets[output] = {}
                    outputArr.push(data.jobUuid)
                    datasets[output]['inputJobs'] = outputArr
                }
            });

        })

        datasets[currentDatasetId]['inputJobs'].map(inputJobs => {
            jobList.push(inputJobs)
            nodesD[inputJobs]['level'] = -1
        })
        let minLevel = 0

        while (jobList.length > 0) {

            let activeJob = jobList[0];
            jobList.shift();
            jobs[activeJob]['inputs'].map(jobInput => {

                nodesD[jobInput]['level'] = nodesD[activeJob]['level'] - 1
                minLevel = Math.min(minLevel, nodesD[jobInput]['level'])

                if (datasets[jobInput].hasOwnProperty('inputJobs')) {
                    datasets[jobInput]['inputJobs'].map(dsInputJob => {
                        jobList.push(dsInputJob)
                        nodesD[dsInputJob]["level"] = nodesD[jobInput]["level"] - 1
                        minLevel = Math.min(minLevel, nodesD[dsInputJob]["level"])
                    })
                }
            })
        }

        Object.values(nodesD).map(node => {
            node.level += Math.abs(minLevel)
        })

        let nodeArr = []
        Object.values(nodesD).map(nodeItem => {
            nodeArr.push(nodeItem)
        })
        
        let linkArr = []
        Object.values(linksD).map(linkItem => {
            linkArr.push(linkItem)
        })

        nodeArr.map((item, i) => {
            item['uuid'] = item.id
            item['id'] = i            
        })
        console.log('nodeArr after', nodeArr)
        setNode(nodeArr)

        linkArr.map((item) => {
            let sourceObj = nodeArr.find(obj => obj.uuid === item.source)
            let targetObj = nodeArr.find(obj => obj.uuid === item.target)
            item.source = sourceObj.id
            item.target = targetObj.id
        })
        console.log('LinkArr after', linkArr)
        setLinks(linkArr)
    }, [])

    return (
        <>
            <LineageChart nodes={nodes} links={links} />
        </>
    )
}

export default (Lineage);