import React, { Fragment, useState } from 'react';
import { Nav, NavItem, Row, Col, TabContent, TabPane } from 'reactstrap';
import { Link } from "react-router-dom";
import './Dashboard.css';
import Overview from './Overview/'

export default function Dashboard() {

    const [activeTab, setActiveTab] = useState('1');

    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>
            <div className="dashboard-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="dashboard-heading">Dashboard</h2>
                    </Col>
                </Row>
                <Row className="navBarRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <Link to="" className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('1'); }}>
                                    Overview
                                </Link>
                            </NavItem>
                            <NavItem>
                                <Link to="" className={activeTab === '2' ? 'nav-link' : 'disable nav-link'} onClick={() => { toggle('3'); }}>
                                    Infosec
                                </Link>
                            </NavItem>
                            <NavItem>
                                <Link to="" className={activeTab === '3' ? 'active nav-link' : 'disable nav-link'}
                                    onClick={() => { toggle('3'); }}>
                                    IT
                                </Link>
                            </NavItem>
                            <NavItem>
                                <Link to="" className={activeTab === '4' ? 'active nav-link' : 'disable nav-link'}
                                    onClick={() => { toggle('4'); }}>
                                    Business
                                </Link>
                            </NavItem>

                        </Nav>
                    </Col>
                </Row>
            </div>
            <div className="tabContent">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Overview />
                    </TabPane>
                    <TabPane tabId="2" disabled={true}>
                        {/* <ListDataset /> */}
                    </TabPane>
                    <TabPane tabId="3">
                        {/* <ListDataset /> */}
                    </TabPane>
                    <TabPane tabId="4">
                        {/* <ListDataset /> */}
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}