import React from 'react';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official';

export default function VolumeProcess() {

    const options = {
        chart: {
            type: 'spline',
            className: 'volume-process'
        },
        title: {
            text: 'Volume of Data Exported',
            align: 'left',
            style: {
                fontSize: '18px',
                color: '#373A3C',
                fontWeight: 'bold'
            }
        },

        xAxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            allowDecimals: false,
            labels: {
                formatter: function () {
                    return this.value; // clean, unformatted number for year
                }
            },
            accessibility: {
                rangeDescription: 'Range: 1940 to 2017.'
            }
        },
        yAxis: {
            allowDecimals: false,
            title: {
                text: 'Volume'
            },
            labels: {
                formatter: function () {
                    return this.value + ' GB';
                }
            },
            min: 0
        },
        tooltip: {
            pointFormat: '<b>{point.y}GB of data processed</b>'
        },
        plotOptions: {
            spline: {

                marker: {
                    enabled: false,
                    symbol: 'circle',
                    radius: 0,
                    states: {
                        hover: {
                            enabled: false
                        }
                    }
                }
            }
        },
        series: [{
            name: 'Volume',
            data: [20, 40, 30, 60, 40, 80, 50, 100, 40, 60, 20, 30
            ]
        }]
    }
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={options}
        />
    )
}
