import React, { Fragment, useState } from 'react';
import { Nav, NavItem,NavLink, Row, Col, TabContent, TabPane } from 'reactstrap';
import './customer.css';
import CustomerOverview from "../customer360/customerOverview/"
import CustomerDictionary from "../customer360/customerDictionary/"
import CustomerQuality from "../customer360/customerQuality/"
import CustomerLineage from "../customer360/customerLineage/"

export default function Customer360() {

    const [activeTab, setActiveTab] = useState('1');
    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>
            <div className="customer-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="customer-heading">Customer 360</h2>
                    </Col>
                </Row>
                <Row className="navBarRow">
                    <Col xs="12">
                        <Nav tabs  style={{paddingLeft:'18rem'}}>
                            <NavItem>
                                <NavLink className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('1'); }}>
                                    Overview
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '2' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('2'); }}>
                                    Dictionary
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '3' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('3'); }}>
                                    Lineage
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '4' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('4'); }}>
                                    Quality
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </Col>
                </Row>
            </div>
            <div className="tabContent"  style={{marginTop:'85px'}}>
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <CustomerOverview/>
                    </TabPane>
                    <TabPane tabId="2">
                        <CustomerDictionary/>
                    </TabPane>
                    <TabPane tabId="3">
                        {activeTab==="3"&&<CustomerLineage />}
                    </TabPane>
                    <TabPane tabId="4">
                        <CustomerQuality/>
                    </TabPane>
                </TabContent>
            </div>

        </Fragment>

    )
}