import React, { Fragment, useState } from 'react';
import { Nav, NavItem, NavLink, Row, Col, TabContent, TabPane } from 'reactstrap';
import '../../customer360/customer.css';
import FormRule from './FormRule/'
import SQLRule from './SQLRule/'
import ChannelsTab from './ChannelsTab';
import DeliveryMode from './DeliveryMode';

export default function CreateRule() {

    const [activeTab, setActiveTab] = useState('1');
    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab);
    }
    return (
        <Fragment>

            <div className="customer-nav fixed-top">
                <Row>
                    <Col xs="12">
                        <h2 className="customer-heading">New Rule</h2>
                    </Col>
                </Row>
                <Row className="navBarRow">
                    <Col xs="12">
                        <Nav tabs>
                            <NavItem>
                                <NavLink className={activeTab === '1' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('1'); }}>
                                    Form
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={activeTab === '2' ? 'active nav-link' : 'nav-link'}
                                    onClick={() => { toggle('2'); }}>
                                    SQL
                                </NavLink>
                            </NavItem>
                        </Nav>
                    </Col>
                </Row>
            </div>
            <Row style={{marginTop:'140px', marginLeft:'100px'}}>
                <Col xs="9">
                    <div className="tabContent m-0">
                        <TabContent activeTab={activeTab} className="m-0">
                            <TabPane tabId="1">
                                <FormRule />
                            </TabPane>
                            <TabPane tabId="2">
                                <SQLRule />
                            </TabPane>
                        </TabContent>
                    </div>
                </Col>
                <Col xs="3">
                    <div>
                        <ChannelsTab />
                        <DeliveryMode />
                    </div>
                </Col>

            </Row>

        </Fragment>

    )
}